.utilityContainer {
    background-color: white;
    flex-direction: row;
    display: flex;
    align-items: center;
    padding-top: 100px;
    color: #1E1457; 
    padding-bottom: 100px;
    justify-content: center;
}


.utilityBanner {
    display: flex;
    text-align: left;
    flex-direction: column;
    justify-content: center;
}

.utilityBannerText {
    font-size: 50px;
    align-content: flex-start;
    display: flex;
    flex-direction: row;
    font-weight: bold ;
}

.utilityBannerSubText {
    font-size: 24px;
}

.utilityContentSubContainer {
    display: flex;
    flex-direction: column  ;
    justify-content: space-around;
}

.utilityContent {
    display: flex;
    flex-direction: row;
    margin-top: 100px;
    align-items: center;   
    justify-content: space-between;
}


.utilityBox {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-size: contain;
    background-position: center;
    color: #1E1457;
    width: 100%;
    height: 150px;
    border-radius: 20px;
}


.utilityBoxHeader {
    font-weight: bold;
    font-size: 24px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.utilityBoxContent {
    font-size: 16px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: center;
    width: 80%;  
    line-height: 30px;
    border: 2px hidden #1E1457;
}


.utilityImage {
    max-width: 450px;
}

@media (max-width: 1400px) {

    .utilityContainer {
        background-color: white;
        flex-direction: row;
        display: flex;
        align-items: center;
        padding-top: 50px;
        color: #1E1457; 
        padding-bottom: 50px;
        justify-content: center;
        padding-left: 50px;
    }

    .utilityBanner {
        display: flex;
        text-align: left;
        flex-direction: column;
        justify-content: center;
        max-width: 100%;
        margin-bottom: 50px;
    }    

    .utilityBannerText {
        font-size: 50px;
        align-content: flex-start;
        display: flex;
        flex-direction: row;
        font-weight: bold ;
    }

    .utilityContent {
        display: flex;
        flex-direction: row;
        margin-top: 0px;
        align-items: center;   
        max-width: 90%;
        justify-content: space-between;
    }

    .utilityContentSubContainer {
        display: flex;
        flex-direction: column  ;
        justify-content: space-around;
        margin-bottom: 50px;
    }
  }

@media only screen and (max-width: 450px) {

    .utilityContainer {
        background-color: white;
        flex-direction: column;
        display: flex;
        align-items: center;
        padding-top: 50px;
        color: #1E1457;
        background-size: 75%;
    }
    .utilityBanner {
        display: flex;
        text-align: left;
        flex-direction: row;
        justify-content: center;
        max-width: 80%;
    }    

    .utilityBannerText {
        font-size: 36px;
        align-content: flex-start;
        display: flex;
        flex-direction: row;
        font-weight: bold ;
    }

    .utilityBox {
        border: 1px hidden #1E1457;
        background-image: none;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;
        width: 90%;
        height: 150px;
        margin-bottom: 45px;
    }

    .utilityContent {
        display: flex;
        flex-direction: column;
        margin-top: 36px;
        align-items: center;   
        max-width: 70%;
        justify-content: space-between;
    }

    .utilityBoxHeader {
        font-weight: bold;
        font-size: 18px;
        margin-top: 0px;
        margin-bottom: 10px;
    }
    
    .utilityBoxContent {
        font-size: 14px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        text-align: center;
        width: 50%;  
        line-height: 20px;
        margin-bottom: 20px;
    }
  
}


