.mintBannerContainer {
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    color: #1E1457;
    margin-top: 50px;
    margin-bottom: 50px;
}

.mintImage {
    max-width: 50%;
    margin-top: 0px;
}

.imageStyle {
    max-width: 600px;
}

.mintContent {
    display: flex;
    flex-direction: column;
    justify-content: left;
    align-items: flex-start ;
    margin-bottom: 0px;
    width: 50%;
}

.mintContentHeader {
    font-size: 40px;
    font-weight: bold;
}

.mintContentDescription {
    font-size: 24px;
    color: grey;    
    margin-top: 20px;
text-align: left    ;
width: 70%;
}

.mintContentInfo {
    font-size: 24px;
    margin-top: 40px;
    margin-bottom: 40px;
    font-weight: normal;
}

.mintContentButton {
    font-size: 16px;
    padding: 20px;
    cursor: pointer;
    border: 2px solid #1E1457;
    background-color: #1E1457;
    border-radius: 20px;
    color: white;
}


