.roadmapContainer {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 20px;
}

.roadmapHeader {
    display: flex;
    justify-content: center;
}

.headerText {
    font-size: 50px;
    font-weight: bold;
    color: #1E1357 ;
}

.roadmapImageContainer {
    display: flex;
    justify-content: center;
    margin-top: 75px;
}

.roadmap {
    max-height: 1000px;
    max-width: 1000px;
}

@media only screen and (max-width: 500px) {
    .roadmapContainer {
        width: 100%;
        display: flex;
        flex-direction: column;
        margin-top: 25px;
    }
    
    .roadmapHeader {
        display: flex;
        justify-content: center;
    }
    
    .headerText {
        font-size: 42px;
        font-weight: bold;
        color: #1E1357 ;
    }
    
    .roadmapImageContainer {
        display: flex;
        justify-content: center;
        margin-top: 25px;
    }
    
    .roadmap {
        max-height: 400px;
        max-width: 400px;
    }
}

