.exploreContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 100px;
  padding-bottom: 100px;
  justify-content: center;
  background-color: white;
}

.exploreContentContainer {
  display: flex;
  flex-direction: row;
  width: 80%;
}

.exploreImageContainer {
  display: flex;
  flex-direction: row;
  width: 50%
}

.exploreImageStyle {
  width: 50%;
}

.exploreContent {
  display: flex;
  flex-direction: row;
}

.exploreContentTextContainer {
  display: flex;
  justify-content: center;
  width: 50%;
}

.exploreContentText {
  display: flex;
  font-size: 40px;
  font-weight: bold;
  color: #1E1357;
  text-align: left;
  align-items: center;
  width: 80%;
  line-height: 80px;

}

@media (max-width: 1400px) {
  .exploreContentContainer {
    display: flex;
    flex-direction: row;
    width: 90%;
  }

  .exploreContentText {
    display: flex;
    font-size: 40px;
    font-weight: bold;
    color: white;
    text-align: left;
    align-items: center;
    width: 80%;
    line-height: 50px;
  
  }

}
@media only screen and (max-width: 850px) {
  .exploreContentContainer {
    display: flex;
    flex-direction: row;
    width: 90%;
  }

  .exploreImageStyle {
    width: 50%;
  }

  .exploreContentText {
    display: flex;
    font-size: 24px;
    font-weight: bold;
    color: white;
    text-align: left;
    align-items: center;
    width: 80%;
    line-height: 25px;
  
  }

}

@media only screen and (max-width: 600px) {
  .exploreContentContainer {
    display: flex;
    flex-direction: column;
    width: 90%;
  }

  .exploreContent {
    display: flex;
    flex-direction: column;
  }

  .exploreImageStyle {
    width: 50%;
    padding:0px
  }

  .exploreImageContainer {
    display: flex;
    flex-direction: row;
    width: 100%;
    margin-bottom: 25px;
  }


  .exploreContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 25px;
    padding-bottom: 25px;
    justify-content: center;
    background-color: white;
  }

  .exploreContentText {
    display: flex;
    font-size: 28px;
    font-weight: bold;
    color: white;
    text-align: center;
    width: 100%;
    line-height: 24px;
    justify-content: center;
  
  }

  .exploreContentTextContainer {
    display: flex;
    width: 100%;
  }
  
}

@media only screen and (max-width: 450px) {
  .exploreContentContainer {
    display: flex;
    flex-direction: column;
    width: 90%;
  }

  .exploreContent {
    display: flex;
    flex-direction: column;
  }

  .exploreImageStyle {
    width: 50%;
    padding:0px
  }

  .exploreImageContainer {
    display: flex;
    flex-direction: row;
    width: 100%;
    margin-bottom: 25px;
  }


  .exploreContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 25px;
    padding-bottom: 25px;
    justify-content: center;
    background-color: white;
  }

  .exploreContentText {
    display: flex;
    font-size: 28px;
    font-weight: bold;
    color: #1E1357;
    text-align: center;
    width: 100%;
    line-height: 24px;
    justify-content: center;
  
  }

  .exploreContentTextContainer {
    display: flex;
    width: 100%;
  }

}

@media only screen and (max-width: 360px) {
  .exploreImageStyle {
    width: 18%;
    padding: 10px; 
  }

  .exploreContentContainer {
    display: flex;
    flex-direction: column;
    width: 95%;
  }

}