.bannerContainer {
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 200px;
  padding-bottom: 100px;

}

.bannerContent {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.headerlogo {
  max-width: 1000px;
  margin-bottom: 10px;
}


.bannerHeader {
  font-weight: normal;
  display: flex;
  flex-direction: column;
  text-align: left;
  max-width: 100%;


}

.headerContent {
  display: flex;
  font-size: 36px;
  text-align: center;
  color: #1E1357;
  width: 100%; 
  justify-content: center;
  font-weight: bold;

}

.headerType {
  font-size: 44px;
  color: #1E1357;
  margin-bottom: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bannerText {
  font-size: 20px;
  display: flex;
  text-align: left;
  margin-bottom: 80px;
  max-width: 70%;
}

.bannerButton {
  display: flex;
  align-items: center;
  padding: 15px;
  margin-right: 50px;
  cursor: pointer;
  color: #1E1357;
  border: 2px solid #1E1357;
  background-color: white;
  border-radius: 10px;
  font-weight: bold;
  font-size: 18px;
}

.bannerButtonOne {
  display: flex;
  align-items: center;
  padding: 15px;
  margin-right: 50px;
  cursor: pointer;
  color: white  ;
  border: 2px solid #1E1357;
  background-color: #1E1357;
  border-radius: 10px;
  font-weight: bold;
  font-size: 18px;
}

.button {
  max-height: 50px;
  margin-right: 10px;
}

.buttonText{
  margin-right: 5px;
  display: flex;
  font-weight: bold ;

}

.textBold {
  font-weight: bold;
  margin-right: 30px;
}


.bannerGIF {
  max-height: 700px;
  border-radius: 10px;
}

.bannerButtonContainer {
  display: flex;
  margin-top: 40px;
  justify-content: center;
}

@media (max-width: 1100px) {
  .bannerContainer {
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 100px;
    padding-bottom: 50px;
  }

  .bannerContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .bannerHeader {
    font-size: 40px;
    font-weight: bolder;
    display: flex;
    flex-direction: column;
    text-align: left;
    max-width: 90%;
    margin-right: 0px;
  
  }

  .headerType {
    font-size: 36px;
    color: #1E1357;
    margin-bottom: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: normal;
  }

  .bannerGIF {
    max-height: 700px;
    border-radius: 10px;
    margin-top: 50px;
  }
}

