.tokenContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 50px;
    color: #1E1457;
}

.tokenlogo {
    max-width: 300px;
}

.tokenContent {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-left: 75px;
    justify-content: center;
}

.tokenLogoHeader {
    font-size: 60px;
    font-weight: bold;
    margin-bottom: 10px;
}

.tokenContentText {
    font-size: 18px;
    cursor: pointer;
    text-decoration: underline;
}

@media (max-width: 1400px) {
    .tokenContainer {
        display: flex;
        flex-direction: column-reverse;
        justify-content: center;
        margin-bottom: 50px;
        color: #1E1457;
    }

    .tokenContent {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-bottom: 25px;
        margin-left: 0px;

    }

    .tokenLogoHeader {
        font-size: 48px;
        margin-bottom: 10px;
    }

    .tokenlogo {
        max-width: 200px;
    }

    .tokenContentText {
        font-size: 14px;
        cursor: pointer;
        text-decoration: underline;
    }
    

}