.teamContainer {
    display: flex;
    color: #1E1357;
    margin-left: 220px;
    margin-top: 100px;
}

.teamContentContainer {
    display: flex;
    flex-direction: column;
}

.teamHeaderContainer {
    display: flex;
    flex-direction: column;
    text-align: left;

}

.teamHeader {
    font-size: 48px;
    font-weight: bold;
}

.teamHeaderText {
    font-size: 24px;
}

.teamInformationContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 100px;
    justify-content: space-around;
}

.teamInformationBox {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 100px;
    width: 33%;

}

.teamImage {
    display: flex;
    flex-direction: row;
}

.profileImage {
    max-width: 200px;
}

.teamContent {
    margin-left: 25px;
    text-align: left;
}

.memberName {
    font-size: 24px;
    font-weight: bold;
}

.memberRole {
    margin-top: 7px;
}

.twitterlink {
    margin-top: 15px;
    width: 25px;
    cursor: pointer
}

.linkedinLink {
    margin-top: 15px;
    width: 25px;
    cursor: pointer;
}

@media only screen and (max-width: 1400px) {
    .teamContainer {
        display: flex;
        color: #1E1357;
        margin-left: 50px;
        margin-top: 80px;
    }

}

